import React from 'react';

interface Props {
  type: string;
  size?: number;
  marginLeft?: number;
  marginRight?: number;
  color?: string;
}

export const Icon = React.memo((props: Props) => {
  return (
    <i
      className={'iconfont icon-' + props.type}
      style={{
        fontSize: props.size ?? 14,
        marginLeft: props.marginLeft ?? 0,
        marginRight: props.marginRight ?? 5,
        color: props?.color ?? 'var(--text-1)',
        transition: 'all 0.3s ease-out'
      }}>
    </i>
  );
});