import React, { useMemo } from 'react';
import { useLocation, Link } from 'react-router-dom';
import { menus } from '../../common/menu';
import { Icon } from '../Icon';
import style from './style.module.scss';

export const MenuList = React.memo(() => {
  const location = useLocation();

  const currentPath = useMemo(() => {
    const matches = location.pathname.split('/');
    return matches[matches.length - 1];
  }, [location.pathname]);
  
  return (
    <div>
      {menus.map((group, gid) => (
        <div key={gid}>
          <div className={style.group}>
            [ {group.title} ]
          </div>
          {group.children.map((item, id) => {
            const isActive = item.path === currentPath;
            const className = style.item + ' ' + (isActive ? style.active : '');
            return (
              <a
                key={id}
                className={className}
                onClick={() => {
                  window.location.href = window.location.pathname + `#/examples/${item.path}`;
                  // window.location.reload();
              }}>
                <span>{item.title}</span>
                {isActive && <Icon type="swords" size={16} marginLeft={10} />}
              </a>
            );
          })}
        </div>
      ))}
    </div>
  )
});