import React from 'react';

interface MenuItemType {
  title: string;
  path: string;
  example: React.LazyExoticComponent<() => JSX.Element>;
}

interface MenuGroupType {
  title: string;
  children: MenuItemType[]
}

export const menus: MenuGroupType[] = [
  {
    title: 'Basic Usage',
    children: [
      {
        title: 'Rotating Cubes',
        path: 'rotating_cubes',
        example: React.lazy(() => import('../examples/rotating_cubes'))
      },
      {
        title: 'Add Skybox',
        path: 'add_skybox',
        example: React.lazy(() => import('../examples/add_skybox'))
      },
      {
        title: 'Load GLTF Model',
        path: 'load_gltf_model',
        example: React.lazy(() => import('../examples/load_gltf_model'))
      },
    ]
  },
  {
    title: 'Camera Controls',
    children: [
      {
        title: 'Orbit Control',
        path: 'orbit_control',
        example: React.lazy(() => import('../examples/orbit_control'))
      },
    ]
  }
];

export const routes = menus.reduce((arr: MenuItemType[], menu) => [...arr, ...menu.children], []);